
@import "normalizes.scss";
@import "resets.scss";

* {
    box-sizing: border-box; }


$color-blue: #4A89E6;
$color-orange: #FF7642;
$color-grey: #CBCBCB;

@font-face {
    font-family: palatino;
    src: url(../font/palatinolinotype_bold.ttf);
    font-weight: bold; }
@font-face {
    font-family: palatino;
    src: url(../font/palatinolinotype_roman.ttf);
    font-weight: regular; }

@font-face {
    font-family: pacifico;
    src: url('../font/Pacifico-Regular.ttf'); }

html {
    font-family: palatino;
    font-weight: regular; }


.container {
    max-width: 1140px;
    margin: 0 auto; }

.main-section {
    position: relative;
    min-height: 800px;
    background: #DFDFDF;
    padding-top: 19px;
    overflow: hidden;
    &__nav-container {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        height: 85px;
        align-items: center; }
    &__logo {
        width: 165px; }
    &__content-container {
        position: relative;
        display: flex;
        flex-direction: row; }
    &__first-elipse {
        position: absolute;
        top: 130px;
        right: 15px; }
    &__second-elipse {
        position: absolute;
        top: 670px;
        right: -70px; } }

.nav-menu {
    &__list {
        display: flex;
        flex-direction: row; }
    &__list-item {
        margin-right: 93px;
        font-size: 18px;
        font-family: pacifico;
        &:last-child {
            margin-right: 0px; }

        a {
            text-decoration: none;
            color: black; } } }


.left-wrapper {
    position: relative;

    img {
        position: relative;
        z-index: 5; }

    &__rectangle-blue {
        position: absolute;
        top: 120px;
        left: -20px;
        width: 530px;
        height: 220px;
        background: $color-blue;
        border-radius: 100px;
        box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
        transform: rotate(-71deg);
        z-index: 2; }


    &__rectangle-orange {
        position: absolute;
        top: 140px;
        left: 4px;
        width: 630px;
        height: 220px;
        background: $color-orange;
        border-radius: 100px;
        box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
        transform: rotate(-45deg);
        z-index: 3; }

    &__rectangle-grey {
        position: absolute;
        top: 165px;
        left: 57px;
        width: 617px;
        height: 220px;
        background: $color-grey;
        border-radius: 100px;
        box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
        transform: rotate(-28deg);
        z-index: 1; } }


.right-wrapper {
    width: 365.01px;
    position: absolute;
    top: 150px;
    left: 739px;
    z-index: 5;
    &__icons-box {
        padding-left: 10px; }
    &__title {
        margin-bottom: 5px; }

    &__text {
        font-size: 16px;
        margin-bottom: 30px; } }


.title {
    font-size: 72px;
    position: relative;
    font-weight: bold;
    &__hr {
        width: 65px;
        position: absolute;
        bottom: 0px;
        margin: 0px;
        border: none;
        border-top: 1px solid black;
        top: 65px;
        &_second {
            width: 55px; } } }


.buttons {
    display: inline-flex;
    &__main-btn {
        margin-right: 12px;
        border-radius: 100px;
        width: 194px;
        height: 52px;
        background: #5d5757;
        box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
        border: none;
        font-size: 19px;
        color: white; }
    &__icon-btn {
        display: inline-flex;
        align-items: center;
        font-size: 16px;
        img {
            margin-right: 5px; } } }

.elipse {
    width: 206px;
    height: 206px;
    background: linear-gradient(135deg, rgba(255, 118, 66, 0.5) 14%, rgba(74, 137, 230, 0.5) 53.1%, rgba(93, 87, 87, 0.5) 81.1%);
    border-radius: 150px;
    display: flex;
    align-items: center;
    justify-content: center;
    &_dig {
        width: 220px;
        height: 220px; }
    &_third {
        width: 300px;
        height: 300px; }

    &__subtract {
        width: 148px;
        height: 148px;
        background: #DFDFDF;
        border-radius: 150px;
        top: 16px;
        left: 33px;
        &_second-sec {
            background: #ffffff; }
        &_third {
            width: 230px;
            height: 230px; }
        &_big {
            width: 162px;
            height: 162px; } } }

.second-section {
    position: relative;
    min-height: 700px;
    background: #ffffff;
    padding-top: 19px;
    overflow: hidden;
    &__title {
        margin-bottom: 42px; }
    &__cards-box {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        margin-bottom: 69px; }
    &__icons-box {
        display: flex;
        flex-direction: row;
        justify-content: space-between; }
    &__first-elipse {
        position: absolute;
        top: 350px;
        left: 120px;
        z-index: 1; }
    &__second-elipse {
        position: absolute;
        top: 70px;
        right: 120px;
        z-index: 1; } }



.cards-item {
    position: relative;
    z-index: 2;
    border-radius: 10px;
    width: 359px;
    height: 359px;
    background: #d9d9d9;
    display: flex;
    align-items: flex-end;
    box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
    img {
        height: 335px; }

    &_orange {
        background: $color-orange;
        padding-left: 40px; }
    &_blue {
        background: $color-blue; }
    &__text {
        width: 280px;
        position: absolute;
        font-size: 36px;
        text-align: center;
        transform: rotate(90deg);
        top: 140px;
        right: -40px;
        font-weight: bold; } }

.icons-item {
    display: inline-flex;
    align-items: center;
    margin-right: 11px;
    &__img {
        width: 80px;
        height: 80px;
        margin-right: 11px; }
    &__text {
        font-size: 20px;
        span {
            font-weight: bold;
            font-size: 48px; } } }

.third-section {
    min-height: 670px;
    background: #DFDFDF;
    position: relative;
    z-index: 0;
    overflow: hidden;
    &__container {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        padding-top: 49px;
        padding-left: 140px; }
    &__right-wrapper {
        max-width: 400px; }
    &__text {
        font-size: 18px;
        margin-bottom: 30px; }
    &__right-title {
        margin-bottom: 20px;
        z-index: 2; }
    &__third-elipse {
        position: absolute;
        top: 130px;
        left: 200px;
        z-index: -2; }

    &__third-circle {
        position: absolute;
        top: 250px;
        left: 70px;
        z-index: -1; }

    &__second-elipse {
        position: absolute;
        top: -70px;
        right: -70px; } }

.footer {
    position: relative;
    &__img-f {
        position: absolute;
        left: 0px;
        bottom: 0px; }
    &_container {
        min-height: 272px;
        display: flex;
        align-items: center;
        justify-content: flex-end; }
    &__title {
        margin-bottom: 18px; } }

@media (max-width: 1199px) {
    .container {
        width: 960px; }
    .main-section {
        min-height: 690px; }
    .second-section {
        min-height: 600px; }
    .left-wrapper {
        img {
            width: 750px; }
        &__rectangle-blue {
            width: 500px;
            height: 200px; }
        &__rectangle-orange {
            width: 540px;
            height: 200px; }
        &__rectangle-grey {
            width: 520px;
            height: 200px; } }
    .right-wrapper {
        left: 580px; }
    .main-section__first-elipse {
        right: 5px; }
    .main-section__second-elipse {
        top: 600px;
        right: -83px; }
    .cards-item {
        width: 300px;
        height: 300px;
        img {
            height: 275px; }
        &__text {
            top: 120px;
            right: -65px;
            font-size: 30px; } }
    .second-section__first-elipse {
        top: 300px;
        left: -67px; }
    .second-section__second-elipse {
        right: -36px; }
    .second-section__cards-box {
        margin-bottom: 40px; }
    .third-section__container {
        padding-left: 89px; }
    .footer__img-f {
        left: -196px;
        bottom: -49px; } }



@media (max-width: 991px) {
    .container {
        width: 720px; } }

@media (max-width: 767px) {
    .container {
        width: 540px; } }

@media (max-width: 575px) {
    .container {
        width: 100%; } }














